@import "../consumables.scss";

$fluid-typography-min-width: $breaks-md;
$fluid-typography-max-width: $breaks-xl;

h1,
h2,
h3,
h4,
h5,
h6,
.heading--1,
.heading--2,
.heading--3,
.heading--4,
.heading--5,
.heading--6 {
  margin: 0;
  color: #ffffff;
  font-family: "Benguiat Bold Condensed BT", sans-serif;
  line-height: 1.1;
}

h1,
.heading--1 {
  @include fluid-property(
    $fluid-typography-min-width,
    $fluid-typography-max-width,
    36px,
    72px,
    font-size
  );

  text-shadow: rgb(0 0 0 / 0.5) 0 0 20px;
  transition: line-height 0.3s ease-in-out;
}

h2,
.heading--2 {
  @include fluid-property(
    $fluid-typography-min-width,
    $fluid-typography-max-width,
    36px,
    62px,
    font-size
  );

  text-shadow: rgb(0 0 0 / 0.5) 0 0 16px;
  transition: line-height 0.3s ease-in-out;
}

h3,
.heading--3 {
  @include fluid-property(
    $fluid-typography-min-width,
    $fluid-typography-max-width,
    19px,
    21px,
    font-size
  );

  font-family: "Dolly Roman", serif;
  line-height: 1.5;
}

p {
  @include fluid-property(
    $fluid-typography-min-width,
    $fluid-typography-max-width,
    19px,
    21px,
    font-size
  );

  color: #e2c06d;
  font-family: "Dolly Roman", serif;
  line-height: 1.5;
}

@media (min-width: $breaks-md) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  .heading--1,
  .heading--2,
  .heading--3,
  .heading--4,
  .heading--5,
  .heading--6 {
    line-height: 1;
  }
}
